import { queryStringPattern } from 'helpers/regex';
export const getQueryParams = (query) => {
    let params = {};
    query.replace(queryStringPattern, (NOT_USED$0, key, NOT_USED$1, value) => {
        key = key.toLowerCase();
        Object.assign(params, { [key]: decodeURIComponent(value) });
    });
    return params;
};
export const getInstanceOfjQuery = (el) => {
    return !(el instanceof jQuery) ? $(el) : el;
};
